@import "mixins";
@import "variables";

/* ===================================================================
========================= SEARCH STYLES ==============================*/


h1 {
  font-weight: $font-weight-extra;
  font-size: $font-size-heading;
}

.search-tweets {
  width: 100%;
  background-color: $background-white;
  box-shadow: $box-shadow-big;
  font-weight: $font-weight-bold;
  padding: $main-spacing-sides;

  @media (max-width: $phone) {
    padding: 20px;
  }

  h1 {
    width: 100%;
    margin: 0;
  }

  .search-description {
    color: $light-grey;
  }


  input {
    height: 75px;
    border-radius: 5px;
    font-family: 'Nunito', sans-serif;
    font-size: $font-size-medium;
    font-weight: $font-weight-bold;
    &:focus,
    &:active {
      outline: none;
    }

  }

  input[type="text"] {
    width: 60%;
    background: none;
    border: 1px solid $light-grey;
    padding-left: 10px;

    &::placeholder {
      color: $dark-grey;
    }

    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  input[type="submit"] {
    margin-left: 20px;
    cursor: pointer;
    width: 17%;
    min-width: 190px;
    background: $blue;
    border: 1px solid $blue;
    color: $background-white;

    @media (max-width: $tablet) {
      margin-top: $main-spacing-bottom-top;
      width: 100%;
      margin: $main-spacing-bottom-top 0 0 0;
    }
  }
}
